@import url("https://fonts.googleapis.com/css?family=Open+Sans");
* {
  font-family: 'Open Sans';
}

header h1 {
  text-align: center;
}

.container {
  width: 300px;
  height: 400px;
  margin: 100px auto;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}

.container .card-container {
  z-index: 10;
  -webkit-perspective: 600px;
          perspective: 600px;
}

.container .card-container:hover .card {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.container .card-container .card {
  width: 300px;
  height: 300px;
  background-color: #eee;
  -webkit-transition: 400ms ease-in-out;
  -o-transition: 400ms ease-in-out;
  transition: 400ms ease-in-out;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  border: 3px solid #666;
}

.container .card-container .card .front {
  width: 300px;
  height: 300px;
  font-size: 2em;
  text-align: center;
  position: absolute;
  overflow: hidden;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  color: #fff;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.container .card-container .card .back {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
  width: 300px;
  height: 300px;
  font-size: 2em;
  text-align: center;
  color: #fff;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.container .card-container .card p {
  padding: 50px;
}

.container button {
  margin: 30px auto;
  display: block;
  border: none;
  background-color: #0275D8;
  color: #fff;
  padding: 10px 30px;
  cursor: pointer;
}

.container button:hover {
  background-color: rgba(2, 117, 216, 0.7);
}

.dummyBox {
  z-index: -1;
  width: 300px;
  height: 300px;
  background-color: #ccc;
  position: absolute;
  border: 3px solid #666;
  top: 150px;
  -webkit-transition: 400ms;
  -o-transition: 400ms;
  transition: 400ms;
}

.goUp {
  -webkit-animation: goUp 400ms;
          animation: goUp 400ms;
}

.dummyChange {
  background-color: #eee;
}

@-webkit-keyframes goUp {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-320px);
            transform: translateY(-320px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes goUp {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-320px);
            transform: translateY(-320px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}


